import { MDCDataTable } from "@material/data-table";
import { useEffect, useRef, useState } from "react";
import { IFutaInfo } from "../repository/futa";
import { IRepositories } from "../repository/repositories";

export function FutaTable(props: {
  repo: IRepositories;
  showTable: boolean;
  moveTo: (lat: number, lng: number) => void;
}) {
  const [hasCount, setHasCount] = useState(0);
  const tableEl = useRef<HTMLDivElement>(null);
  const futaList = props.repo.futa.getAll();

  useEffect(() => {
    if (tableEl.current == null) return;
    const dataTable = new MDCDataTable(tableEl.current!);
  }, [tableEl.current]);

  useEffect(() => {
    const count = props.repo.futa.calcHasCount();
    setHasCount(count);
  }, [props.repo]);

  if (!props.showTable) {
    return <div></div>;
  }

  return (
    <div
      className="mdc-card"
      style={{
        position: "fixed",
        top: "80px",
        left: "16px",
        right: "16px",
        bottom: "16px",
        overflowY: "scroll",
      }}
    >
      <div>
        <p style={{ paddingLeft: "16px" }}>
          {hasCount} / {futaList.length}
        </p>
      </div>
      <div ref={tableEl} className="mdc-data-table">
        <div className="mdc-data-table__table-container">
          <table className="mdc-data-table__table" aria-label="futa">
            <thead>
              <tr className="mdc-data-table__header-row">
                <th
                  className="mdc-data-table__header-cell"
                  role="columnheader"
                  scope="col"
                ></th>
                <th
                  className="mdc-data-table__header-cell mdc-data-table__header-cell--numeric"
                  role="columnheader"
                  scope="col"
                >
                  ID
                </th>
                <th
                  className="mdc-data-table__header-cell"
                  role="columnheader"
                  scope="col"
                >
                  
                </th>
              </tr>
            </thead>
            <tbody className="mdc-data-table__content">
              {futaList.map((futa) => (
                <FutaTr
                  key={futa.id}
                  repo={props.repo}
                  futa={futa}
                  moveTo={props.moveTo}
                />
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

function FutaTr(props: {
  repo: IRepositories;
  futa: IFutaInfo;
  moveTo: (lat: number, lng: number) => void;
}) {
  const data = props.repo.futa.getDataById(props.futa.id);
  const hasFuta = data.isFound || data.isGiven;

  return (
    <tr
      className="mdc-data-table__row"
      onClick={() => props.moveTo(props.futa.lat, props.futa.lng)}
    >
      <td className="mdc-data-table__cell">{hasFuta ? "" : ""}</td>
      <th
        className="mdc-data-table__cell mdc-data-table__cell--numeric"
        scope="row"
      >
        {props.futa.id}
      </th>
      <td className="mdc-data-table__cell">{props.futa.title}</td>
    </tr>
  );
}
