import { MDCRipple } from "@material/ripple";
import * as React from "react";

export default class ContainedButton extends React.Component<{
  className?: string;
  style?: React.CSSProperties;
  onClick: () => void;
}> {
  private rootEl: React.RefObject<HTMLButtonElement>;
  private ripple: MDCRipple | null = null;

  constructor(props: any) {
    super(props);
    this.rootEl = React.createRef();
  }

  componentDidMount() {
    if (this.rootEl.current == null) return;
    this.ripple = new MDCRipple(this.rootEl.current!);
  }

  componentWillUnmount() {
    this.ripple?.destroy();
  }

  render() {
    const className = this.props.className
      ? `mdc-button mdc-button--raised ${this.props.className}`
      : `mdc-button mdc-button--raised`;
    return (
      <button
        ref={this.rootEl}
        className={className}
        style={this.props.style}
        onClick={this.props.onClick}
      >
        <span className="mdc-button__label">{this.props.children}</span>
      </button>
    );
  }
}
