import { Status, Wrapper } from "@googlemaps/react-wrapper";
import React, { useEffect, useRef, useState } from "react";
import "./App.css";
import "./mdc.scss";
import { newModule } from "./repository/module";
import { IRepositories } from "./repository/repositories";
import TopScreen from "./screen/TopScreen";

import { initializeApp } from "firebase/app";
import { getAuth, onAuthStateChanged, User } from "firebase/auth";
import {
  collection,
  doc,
  getDocs,
  getFirestore,
  query,
} from "firebase/firestore";
import { IFutaData } from "./repository/futa";

const firebaseAPIKey = window.atob(
  "QUl6YVN5Qkw4QTdGNWlnQl" + "YtQUpWRnpFbENuZlk0QWpMTHppUDlr"
);
const mapAPIKey = window.atob(
  "QUl6YVN5RHRldnlseEF0a" + "kg4alVyZjEwZnpWV2kzdlUtdEZ6X0RN"
);
function App() {
  const [repo, setRepo] = useState<IRepositories>();
  const render = (status: Status) => {
    return <h1>{status}</h1>;
  };

  useEffect(() => {
    if (repo !== undefined) return;
    const f = async () => {
      const firebaseApp = initializeApp({
        apiKey: firebaseAPIKey,
        authDomain: "pgo-report.firebaseapp.com",
        projectId: "pgo-report",
        storageBucket: "pgo-report.appspot.com",
        messagingSenderId: "181709832260",
        appId: "1:181709832260:web:fdf93e73251ce22effbc11",
        measurementId: "G-MMFHVHH80W",
      });
      const auth = getAuth(firebaseApp);
      const db = getFirestore(firebaseApp);

      onAuthStateChanged(auth, async (user: User | null) => {
        console.log("onAuthStateChanged");
        if (user == null) {
          const r = newModule(user, [], auth, db);
          setRepo(r);
          return;
        }
        // fetch futa data
        const accounts = collection(db, "accounts");
        const accountDocRef = doc(accounts, user.uid);
        const futaCollection = collection(accountDocRef, "futa");
        const getAllQuery = query(futaCollection);
        const futaDocs = await getDocs(getAllQuery);
        if (futaDocs.empty) {
          const r = newModule(user, [], auth, db);
          setRepo(r);
          return;
        }
        const out: IFutaData[] = [];
        futaDocs.forEach((futaDoc) => {
          const data = futaDoc.data();
          if (data === undefined) {
            return;
          }
          const id = parseInt(futaDoc.id.substring("id_".length));
          out.push({
            id: id,
            isFound: data["found"] as boolean,
            isGiven: data["given"] as boolean,
          });
        });
        const r = newModule(user, out, auth, db);
        setRepo(r);
      });
    };
    f();
  }, [repo]);

  return (
    <Wrapper apiKey={mapAPIKey} render={render}>
      {repo ? <TopScreen repo={repo} /> : null}
    </Wrapper>
  );
}

export default App;
