export default function TopAppBar(props: {
  title: string;
  hasBackButton?: boolean;
  actionButtons?: IActionButton[];
  onActionButtonClicked?: (id: string) => void;
}) {
  const backButton = props.hasBackButton ? (
    <button
      className="material-icons mdc-top-app-bar__navigation-icon mdc-icon-button"
      aria-label="Open navigation menu"
      onClick={() => window.history.back()}
    >
      arrow_back
    </button>
  ) : null;
  const actionButtons = props.actionButtons ? (
    <section
      className="mdc-top-app-bar__section mdc-top-app-bar__section--align-end"
      role="toolbar"
    >
      {props.actionButtons.map((button) => (
        <button
          key={button.id}
          className="material-icons mdc-top-app-bar__action-item mdc-icon-button"
          aria-label={button.icon}
          onClick={() => props.onActionButtonClicked!(button.id)}
        >
          {button.icon}
        </button>
      ))}
    </section>
  ) : null;

  return (
    <header className="mdc-top-app-bar">
      <div className="mdc-top-app-bar__row">
        <section className="mdc-top-app-bar__section mdc-top-app-bar__section--align-start">
          {backButton}
          <span className="mdc-top-app-bar__title">{props.title}</span>
          {actionButtons}
        </section>
      </div>
    </header>
  );
}

export interface IActionButton {
  id: string;
  icon: string;
}
