const futaMap = futaList.reduce((v, info) => {
  v[`${info.id}`] = info;
  return v;
}, {} as { [key: string]: IFutaInfo });

export default class FutaRepository implements IFutaRepository {
  constructor(
    private user: User | null,
    private futaData: IFutaData[],
    private db: Firestore
  ) {}
  getAll(): IFutaInfo[] {
    return futaList;
  }
  getInfoById(id: number): IFutaInfo {
    return futaMap[`${id}`];
  }
  getDataById(id: number): IFutaData {
    const data = this.futaData.find((data) => data.id == id);
    return data === undefined
      ? {
          id: id,
          isFound: false,
          isGiven: false,
        }
      : data;
  }
  async saveData(id: number, found: boolean, given: boolean): Promise<void> {
    if (this.user == null) return;

    const accounts = collection(this.db, "accounts");
    const accountDocRef = doc(accounts, this.user.uid);
    const futaCollection = collection(accountDocRef, "futa");
    const futaDocRef = doc(futaCollection, `id_${id}`);
    await setDoc(futaDocRef, {
      found: found,
      given: given,
    });
  }
  calcHasCount(): number {
    return this.futaData
      .map((data) => (data.isFound || data.isGiven ? 1 : 0))
      .reduce((sum, v) => sum + v, 0 as number);
  }
}
