import { IAccount, IAccountRepository } from "../account";
import { Auth, signInWithEmailAndPassword, User } from "firebase/auth";

export default class AccountRepository implements IAccountRepository {
  constructor(private auth: Auth, private user: User | null) {}
  async login(email: string, password: string): Promise<IAccount> {
    const user = await signInWithEmailAndPassword(this.auth, email, password);
    return {};
  }
  async logout(): Promise<void> {
    this.auth.signOut();
  }

  isLoggedIn(): boolean {
    return this.user != null;
  }
}
