import { MDCDialog } from "@material/dialog";
import { report } from "process";
import React, { useEffect, useRef, useState } from "react";
import { IFutaData, IFutaInfo } from "../repository/futa";
import { IRepositories } from "../repository/repositories";
import Checkbox from "../widget/Checkbox";
import ContainedButton from "../widget/ContainedButton";
import { Scaffold } from "../widget/Scaffold";
import TextField from "../widget/TextField";
import TopAppBar, { IActionButton } from "../widget/TopAppBar";
import { FutaTable } from "./FutaTable";
import "./TopScreen.scss";

const loginButtons: IActionButton[] = [
  { id: "list", icon: "list" },
  { id: "login", icon: "account_circle" },
];
const logoutButtons: IActionButton[] = [
  { id: "list", icon: "list" },
  { id: "logout", icon: "logout" },
];

export default function TopScreen(props: { repo: IRepositories }) {
  const [showTable, setShowTable] = useState(false);

  const mapEl = useRef<Map>(null);
  const editDialog = useRef<EditDialog>(null);
  const loginDialog = useRef<LoginDialog>(null);
  const logoutDialog = useRef<LogoutDialog>(null);

  const updateData = (info: IFutaInfo, found: boolean, given: boolean) => {
    const f = async () => {
      try {
        await props.repo.futa.saveData(info.id, found, given);
        console.log("saved!");
      } catch (e) {
        console.log(e);
      }
    };
    f();
  };
  const onActionButtonClicked = (id: string) => {
    console.log(id);
    if (id === "login") {
      loginDialog.current?.open();
      return;
    }
    if (id === "logout") {
      logoutDialog.current?.open();
      return;
    }
    if (id === "list") {
      setShowTable(!showTable);
      return;
    }
  };
  const onLogin = (email: string, password: string) => {
    const f = async () => {
      try {
        await props.repo.account.login(email, password);
        loginDialog.current?.close();
      } catch (e) {
        console.log(e);
      }
    };
    f();
  };
  const onAcceptLogout = () => {
    const f = async () => {
      await props.repo.account.logout();
    };
    f();
  };
  const moveTo = (lat: number, lng: number) => {
    setShowTable(false);
    mapEl.current?.moveTo(lat, lng);
  };

  useEffect(() => {
    (window as any).found = (id: number) => {
      console.log(`id=${id} clicked!`);
      if (!props.repo.account.isLoggedIn()) {
        loginDialog.current?.open();
        return;
      }
      const info = props.repo.futa.getInfoById(id);
      const data = props.repo.futa.getDataById(id);
      editDialog.current?.open(info, data);
    };
  }, [props.repo]);

  return (
    <Scaffold
      topBar={
        <TopAppBar
          title={"PokeFuta"}
          actionButtons={
            props.repo.account.isLoggedIn() ? logoutButtons : loginButtons
          }
          onActionButtonClicked={onActionButtonClicked}
        />
      }
    >
      <div className="top">
        <Map ref={mapEl} repo={props.repo} />
        <EditDialog ref={editDialog} onAccept={updateData} />
        <LoginDialog ref={loginDialog} onLogin={onLogin} />
        <LogoutDialog ref={logoutDialog} onAccept={onAcceptLogout} />
        <FutaTable repo={props.repo} showTable={showTable} moveTo={moveTo} />
      </div>
    </Scaffold>
  );
}

class Map extends React.Component<
  {
    repo: IRepositories;
    children?: React.ReactElement[];
  },
  {
    map: google.maps.Map | undefined;
    markers: google.maps.Marker[];
    infoWindow: google.maps.InfoWindow;
  }
> {
  private mapEl: React.RefObject<HTMLDivElement>;

  constructor(props: any) {
    super(props);
    this.mapEl = React.createRef();
    this.state = {
      map: undefined,
      markers: [],
      infoWindow: new google.maps.InfoWindow(),
    };
  }

  componentDidMount() {
    if (this.mapEl.current == null) return;
    const map = new google.maps.Map(this.mapEl.current, {
      center: { lat: 35.6862559, lng: 139.741776 },
      zoom: 14,
    });
    this.state.markers.forEach((marker) => {
      marker.setMap(null);
    });
    const newMarkers = this.props.repo.futa.getAll().map((futa) => {
      const data = this.props.repo.futa.getDataById(futa.id);
      const hasFuta = data.isFound || data.isGiven;

      const marker = new google.maps.Marker({
        position: { lat: futa.lat, lng: futa.lng },
        map: map,
        title: `${futa.id}:${futa.title}`,
        icon: hasFuta
          ? "https://maps.google.com/mapfiles/ms/icons/green-dot.png"
          : "https://maps.google.com/mapfiles/ms/icons/red-dot.png",
      });
      marker.addListener("click", () => {
        this.state.infoWindow.close();
        this.state.infoWindow.setContent(
          `<div>
          <h2>${marker.getTitle()}</h2>
          <p><a href="https://local.pokemon.jp/manhole/desc/${
            futa.id
          }/" target="_blank" rel="oppener"></a></p>
          <button onclick="found(${futa.id})"></button></div>`
        );
        this.state.infoWindow.open(marker.getMap(), marker);
      });
      return marker;
    });

    this.setState({
      map: map,
      markers: newMarkers,
    });
  }

  render() {
    return <div ref={this.mapEl} id="map"></div>;
  }

  moveTo(lat: number, lng: number) {
    this.state.map?.panTo(new google.maps.LatLng(lat, lng));
  }
}

class EditDialog extends React.Component<
  {
    onAccept: (info: IFutaInfo, found: boolean, given: boolean) => void;
  },
  {
    info: IFutaInfo | undefined;
    found: boolean;
    given: boolean;
  }
> {
  private rootEl: React.RefObject<HTMLDivElement>;
  private foundEl: React.RefObject<Checkbox>;
  private givenEl: React.RefObject<Checkbox>;
  private dialog!: MDCDialog;

  constructor(props: any) {
    super(props);
    this.rootEl = React.createRef();
    this.foundEl = React.createRef();
    this.givenEl = React.createRef();
    this.state = { info: undefined, found: false, given: false };
  }

  componentDidMount() {
    if (this.rootEl.current == null) return;
    this.dialog = new MDCDialog(this.rootEl.current!);
    this.dialog.listen("MDCDialog:opened", (e: Event) => {
      this.foundEl.current?.setCheck(this.state.found);
      this.givenEl.current?.setCheck(this.state.given);
    });
    this.dialog.listen("MDCDialog:closing", (e: Event) => {
      const action = (e as any).detail.action;
      if (action === "accept") {
        const info = this.state.info;
        const found = this.foundEl.current?.isChecked() ?? false;
        const given = this.givenEl.current?.isChecked() ?? false;

        if (info === undefined) return;
        this.props.onAccept(info, found, given);
      }
    });
  }

  open(info: IFutaInfo, data: IFutaData) {
    this.dialog.open();
    this.setState({
      info: info,
      found: data.isFound,
      given: data.isGiven,
    });
  }

  render() {
    return (
      <div ref={this.rootEl} className="mdc-dialog">
        <div className="mdc-dialog__container">
          <div
            className="mdc-dialog__surface"
            role="alertdialog"
            aria-modal="true"
            aria-labelledby="my-dialog-title"
            aria-describedby="my-dialog-content"
          >
            <div className="mdc-dialog__content" id="my-dialog-content">
              {this.state.info ? (
                <>
                  <h2>
                    {this.state.info.id}:{this.state.info.title}
                  </h2>
                  <div>
                    <Checkbox
                      ref={this.foundEl}
                      id="found"
                      label=""
                    />
                  </div>
                  <div>
                    <Checkbox ref={this.givenEl} id="given" label="" />
                  </div>
                  <div className="mdc-dialog__actions">
                    <button
                      type="button"
                      className="mdc-button mdc-dialog__button"
                      data-mdc-dialog-action="close"
                    >
                      <div className="mdc-button__ripple"></div>
                      <span className="mdc-button__label">Cancel</span>
                    </button>
                    <button
                      type="button"
                      className="mdc-button mdc-dialog__button"
                      data-mdc-dialog-action="accept"
                    >
                      <div className="mdc-button__ripple"></div>
                      <span className="mdc-button__label">OK</span>
                    </button>
                  </div>
                </>
              ) : null}
            </div>
          </div>
        </div>
        <div className="mdc-dialog__scrim"></div>
      </div>
    );
  }
}

class LoginDialog extends React.Component<{
  onLogin: (email: string, password: string) => void;
}> {
  private rootEl: React.RefObject<HTMLDivElement>;
  private emailEl: React.RefObject<TextField>;
  private passwordEl: React.RefObject<TextField>;
  private buttonEl: React.RefObject<ContainedButton>;
  private dialog!: MDCDialog;

  constructor(props: any) {
    super(props);
    this.rootEl = React.createRef();
    this.emailEl = React.createRef();
    this.passwordEl = React.createRef();
    this.buttonEl = React.createRef();

    this.onClick = this.onClick.bind(this);
  }

  componentDidMount() {
    if (this.rootEl.current == null) return;
    this.dialog = new MDCDialog(this.rootEl.current!);
  }

  open() {
    this.dialog.open();
  }

  close() {
    this.dialog.close();
  }

  onClick() {
    const email = this.emailEl.current?.getValue() ?? "";
    const password = this.passwordEl.current?.getValue() ?? "";

    this.props.onLogin(email, password);
  }

  render() {
    return (
      <div ref={this.rootEl} className="mdc-dialog">
        <div className="mdc-dialog__container">
          <div
            className="mdc-dialog__surface"
            role="alertdialog"
            aria-modal="true"
            aria-labelledby="my-dialog-title"
            aria-describedby="my-dialog-content"
          >
            <div className="mdc-dialog__content" id="my-dialog-content">
              <h2></h2>
              <div>
                <TextField
                  ref={this.emailEl}
                  style={{ width: "100%" }}
                  id="email"
                  label=""
                  defaultValue=""
                />
              </div>
              <div>
                <TextField
                  ref={this.passwordEl}
                  style={{ width: "100%" }}
                  id="password"
                  type="password"
                  label=""
                  defaultValue=""
                />
              </div>
              <div style={{ textAlign: "right" }}>
                <ContainedButton ref={this.buttonEl} onClick={this.onClick}>
                  
                </ContainedButton>
              </div>
            </div>
          </div>
        </div>
        <div className="mdc-dialog__scrim"></div>
      </div>
    );
  }
}

class LogoutDialog extends React.Component<{
  onAccept: () => void;
}> {
  private rootEl: React.RefObject<HTMLDivElement>;
  private dialog!: MDCDialog;

  constructor(props: any) {
    super(props);
    this.rootEl = React.createRef();
  }

  componentDidMount() {
    if (this.rootEl.current == null) return;
    this.dialog = new MDCDialog(this.rootEl.current!);
    this.dialog.listen("MDCDialog:closing", (e: Event) => {
      const action = (e as any).detail.action;
      if (action === "accept") {
        this.props.onAccept();
      }
    });
  }

  open() {
    this.dialog.open();
  }

  render() {
    return (
      <div ref={this.rootEl} className="mdc-dialog">
        <div className="mdc-dialog__container">
          <div
            className="mdc-dialog__surface"
            role="alertdialog"
            aria-modal="true"
            aria-labelledby="my-dialog-title"
            aria-describedby="logout-dialog-content"
          >
            <div className="mdc-dialog__content" id="logout-dialog-content">
              
            </div>
            <div className="mdc-dialog__actions">
              <button
                type="button"
                className="mdc-button mdc-dialog__button"
                data-mdc-dialog-action="cancel"
              >
                <div className="mdc-button__ripple"></div>
                <span className="mdc-button__label"></span>
              </button>
              <button
                type="button"
                className="mdc-button mdc-dialog__button"
                data-mdc-dialog-action="accept"
              >
                <div className="mdc-button__ripple"></div>
                <span className="mdc-button__label"></span>
              </button>
            </div>
          </div>
        </div>
        <div className="mdc-dialog__scrim"></div>
      </div>
    );
  }
}

