import React from "react";

import { MDCFormField } from "@material/form-field";
import { MDCCheckbox } from "@material/checkbox";

export default class Checkbox extends React.Component<{
  id: string;
  label: string;
  className?: string;
  style?: React.CSSProperties;
  type?: string;
}> {
  private rootEl!: React.RefObject<HTMLDivElement>;
  private checkboxEl!: React.RefObject<HTMLDivElement>;
  private checkBox!: MDCCheckbox;
  private formField!: MDCFormField;

  constructor(props: any) {
    super(props);
    this.rootEl = React.createRef();
    this.checkboxEl = React.createRef();
  }

  setCheck(checked: boolean) {
    this.checkBox.checked = checked;
  }

  isChecked(): boolean {
    return this.checkBox.checked;
  }

  componentDidMount() {
    if (this.rootEl.current == null) return;
    if (this.checkboxEl.current == null) return;
    this.checkBox = new MDCCheckbox(this.checkboxEl.current);
    this.formField = new MDCFormField(this.rootEl.current);
    this.formField.input = this.checkBox;
  }

  componentWillUnmount() {
    this.checkBox.destroy();
    this.formField.destroy();
  }

  render() {
    return (
      <div ref={this.rootEl} className="mdc-form-field">
        <div ref={this.checkboxEl} className="mdc-checkbox">
          <input
            type="checkbox"
            className="mdc-checkbox__native-control"
            id={this.props.id}
          />
          <div className="mdc-checkbox__background">
            <svg className="mdc-checkbox__checkmark" viewBox="0 0 24 24">
              <path
                className="mdc-checkbox__checkmark-path"
                fill="none"
                d="M1.73,12.91 8.1,19.28 22.79,4.59"
              />
            </svg>
            <div className="mdc-checkbox__mixedmark"></div>
          </div>
          <div className="mdc-checkbox__ripple"></div>
        </div>
        <label htmlFor={this.props.id}>{this.props.label}</label>
      </div>
    );
  }
}
