import AccountRepository from "./account/accountRepository";
import FutaRepository from "./futa/futaRepository";
import { IRepositories } from "./repositories";
import { Auth, User } from "firebase/auth";
import { Firestore } from "firebase/firestore";
import { IFutaData } from "./futa";

export const newModule = (
  user: User | null,
  futaData: IFutaData[],
  auth: Auth,
  db: Firestore
) => {
  return {
    account: new AccountRepository(auth, user),
    futa: new FutaRepository(user, futaData, db),
  } as IRepositories;
};
